.result {
  width: 99.4%;
  height: 70px;
  font-size: 60px;
  background-color: grey;
  color: aliceblue;
}
.btnCont {
  height: 90vh;
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  gap: 0;
}
.btnCont button {
  font-size: 60px;
  text-align: center;
}
.btnCont button:nth-child(17) {
  grid-column: 1/3;
}
.btnCont button:nth-child(1),
.btnCont button:nth-child(2),
.btnCont button:nth-child(3),
.btnCont button:nth-child(4),
.btnCont button:nth-child(8),
.btnCont button:nth-child(12),
.btnCont button:nth-child(16),
.btnCont button:nth-child(19),
.btnCont button:nth-child(1) {
  background-color: rgb(201, 114, 0);
  color: white;
}
